import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { xml2json } from 'xml-js';
import { interval } from 'rxjs';
import { switchMap } from 'rxjs/operators';

@Component({
  selector: 'app-rss-feed',
  templateUrl: './rss-feed.component.html',
  styleUrls: ['./rss-feed.component.css']
})

export class RssFeedComponent implements OnInit {
  rssData: any;
  rssUrl = 'https://rss.walla.co.il/feed/22';

  constructor(private http: HttpClient) { }

  ngOnInit() {
    this.getRssData();

    interval(30000)
    .pipe(
      switchMap(() => this.updateRssData())
    )
    .subscribe();
  }

  getRssData() {
    this.http.get(this.rssUrl, { responseType: 'text' }).subscribe(data => {
      this.rssData = this.convert(data);
    });
  }

  updateRssData() {
    return this.http.get(this.rssUrl, { responseType: 'text' }).pipe(
      switchMap(data => {
        this.rssData = this.convert(data);
        return data;
      })
    );
  }

  convert(data: any) {
    const result = JSON.parse(xml2json(data, { compact: true, spaces: 4 }));
    const items = result['rss']['channel'].item;
    return items.map((item: any) => ({title: item.title._cdata, date: item.pubDate._text}));
  }
}
