import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { interval, throwError } from 'rxjs';
import { catchError, switchMap, timeout } from 'rxjs/operators';
import { environment } from '../../environments/environment';

export interface Alert {
  alertDate: string;
  title: string;
  data: string;
  category: number;
}

@Component({
  selector: 'app-alerts-feed',
  templateUrl: './alerts-feed.component.html',
  styleUrls: ['./alerts-feed.component.css']
})

export class AlertsFeedComponent implements OnInit {
  url = '';
  runEvery = 10 * 1000;
  alertsData: Alert[] = [];
  noResponse = false;

  constructor(private http: HttpClient) {}

  ngOnInit() {
    this.url = environment.apiUrl;
    this.fetchAlertsData();

    interval(this.runEvery).subscribe(() => {
      this.fetchAlertsData();
    });
  }

  fetchAlertsData() {  
    this.http.get<Alert[]>(this.url)
      .pipe(
        timeout(this.runEvery * 2.2),
        catchError(error => {
          console.error('Error fetching data:', error);
          this.noResponse = true;
          return throwError('Data not available');
        })
      )
      .subscribe(data => {
        this.alertsData = data;
        this.noResponse = false;
      });
  }
}
